export default {
  props: ['user'],
  data () {
    return {
      hot: {}
    }
  },
  mounted () {
    this.$axios.api({
      key: 'USER_ALL_ARTCILE',
      data: { id: this.user.id }
    })
      .get()
      .then(res => (this.hot = res))
  }
}
